
body{
  padding: 0;
  margin: 0;
  color: #2c3e50;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F6F6F6;
  font-size: 14px;
}
header {
  font-family: 'Cabin Sketch', 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  padding-top: 50px;
  padding-bottom: 15px;
  background: #f3f3f3;
}
main {
  margin: 0 auto;
  padding: 15px;
  max-width: 800px;
  margin-bottom: 100px;
  background: #fff;
}
blockquote {
  margin: 0;
  padding: 0.05em 1em;
  color: #6a737d;
  border-left: 0.25em solid #dfe2e5;
  background: #f3f3f3;
}
a{
  color: #0366D6;
  text-decoration: none;
}
.hljs{
  background: #FAFAFA;
}
code {
  color: #ED4C6B;
  background-color: #F6F8FA;
  margin: 0 4px;
  display: inline-block;
  padding: 1px 5px;
  font-size: 12px;
  border-radius: 3px;
  line-height: 18px;
  font-family: "SFMono-Regular",Consolas,"Liberation Mono",Menlo,Courier,monospace;
}
.doc-block table {
  border-collapse: collapse;
  width: 100%;
  background-color: #fff;
  font-size: 14px;
  margin-bottom: 45px;
  line-height: 1.5em;
  text-align: left;
}
.doc-block table td,
.doc-block table th
{
  border-bottom: 1px solid #d8d8d8;
  padding: 15px;
  max-width: 250px;
}

.layout-footer{
  height: 100px;
}
.copyright{
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  color: #586069;
  font-weight: 100;
}
.copyright li {
  display: inline-block;
  margin-left: 10px;
}

.fork-img{
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}

.demo-block{
  border: 1px solid #ebebeb;
  border-radius: 3px;
  -webkit-transition: .2s;
  transition: .2s;
  padding: 0px;
  margin: 10px 0;
}
.demo-block .source{
  padding: 24px;
}
.demo-block .description {
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  font-size: 14px;
  line-height: 22px;
  color: #666;
  word-break: break-word;
  margin: 10px;
  background-color: #fff;
}
.demo-block .description p{
  padding: 0;
  margin: 0;
}
.demo-block .meta {
  background-color: #fafafa;
  border-top: 1px solid #eaeefb;
  overflow: hidden;
}
.demo-block body,
.demo-block .meta .hljs{
  background: transparent;
}

a.m-button {
  text-decoration: none;
  color: inherit;
}
.m-button {
  color: #fff;
  background-image: none;
  background-color: #727B84;
  display: inline-block;
  padding: 7px 21px;
  font-size: 14px;
  font: inherit;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  cursor: pointer;
  outline: none;
  border-radius: 4px;
  border: solid 1px #727B84;
  -webkit-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  user-select: none;
  moz-user-select: -moz-none;
  -moz-user-select: none;
  -o-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.m-button:hover {
  background-color: #868E96;
  border: solid 1px #868E96;
}
.m-button:active,
.m-button.active {
  opacity: 0.9;
  position: relative;
}
.m-button.active:after {
  content: '';
  position: absolute;
  display: block;
  top: -4px;
  left: -4px;
  bottom: -4px;
  right: -4px;
  opacity: 0.35;
  border: solid 6px;
  border-radius: 6px;
  border-color: inherit;
}
.m-button + .m-button {
  margin-left: 10px;
}
.m-button:disabled,
.m-button.disabled {
  cursor: not-allowed;
  color: rgba(0, 0, 0, 0.25);
  background-color: #f5f5f5;
  border-color: #d9d9d9;
}
.m-button.m-button-rounded {
  border-radius: 50px;
}
.m-button.m-button-max {
  padding: 15px 45px;
  font-size: 18px;
}
.m-button.m-button-large {
  padding: 10px 30px;
}
.m-button.m-button-small {
  padding: 5px 15px;
  font-size: 13px;
}
.m-button.m-button-mini {
  padding: 4px 12px;
  font-size: 12px;
}
.m-button.m-button-block {
  display: block;
  width: 100%;
}
.m-button.m-button-block + .m-button.m-button-block {
  margin-left: auto;
  margin-top: 10px;
}
.m-button.m-button-info {
  color: #fff;
  background-color: #2DB7F5;
  border-color: #2DB7F5;
}
.m-button.m-button-info:hover {
  background-color: #36C1FA;
  border-color: #36C1FA;
}
.m-button.m-button-info:disabled,
.m-button.m-button-info.disabled {
  cursor: not-allowed;
  background-color: #6BCCEA;
  border-color: #6BCCEA;
}
.m-button.m-button-primary {
  color: #fff;
  background-color: #7952B3;
  border-color: #7952B3;
}
.m-button.m-button-primary:hover {
  background-color: #8E74B4;
  border-color: #8E74B4;
}
.m-button.m-button-primary:disabled,
.m-button.m-button-primary.disabled {
  cursor: not-allowed;
  background-color: #9E98CE;
  border-color: #9E98CE;
}
.m-button.m-button-success {
  color: #fff;
  background-color: #03B976;
  border-color: #03B976;
}
.m-button.m-button-success:hover {
  background-color: #05a56a;
  border-color: #05a56a;
}
.m-button.m-button-success:disabled,
.m-button.m-button-success.disabled {
  cursor: not-allowed;
  background-color: #56ceb0;
  border-color: #56ceb0;
}
.m-button.m-button-danger {
  color: #fff;
  background-color: #F56C6C;
  border-color: #F56C6C;
}
.m-button.m-button-danger:hover {
  background-color: #F78989;
  border-color: #F78989;
}
.m-button.m-button-danger:disabled,
.m-button.m-button-danger.disabled {
  cursor: not-allowed;
  background-color: #FAB6B6;
  border-color: #FAB6B6;
}
.m-button.m-button-warning {
  color: #fff;
  background-color: #F9AA40;
  border-color: #F9AA40;
}
.m-button.m-button-warning:hover {
  background-color: #FAB458;
  border-color: #FAB458;
}
.m-button.m-button-warning:disabled,
.m-button.m-button-warning.disabled {
  cursor: not-allowed;
  background-color: #ffc576;
  border-color: #ffc576;
}
.m-button.clicked {
  position: relative;
}
.m-button.clicked:before {
  content: '';
  position: absolute;
  display: block;
  top: -1px;
  left: -1px;
  bottom: -1px;
  right: -1px;
  opacity: .8;
  border: solid 0px;
  border-color: inherit;
  border-radius: inherit;
  -webkit-animation: btn-clicked-effect 0.5s;
          animation: btn-clicked-effect 0.5s;
}
.m-button.m-button-type-plain {
  color: #868E96;
  border-color: #727B84;
  background-color: transparent;
}
.m-button.m-button-type-plain:hover {
  background-color: #727B84;
  color: #fff;
}
.m-button.m-button-type-plain.m-button-info {
  color: #2DB7F5;
  border-color: #36C1FA;
}
.m-button.m-button-type-plain.m-button-info:hover {
  border-color: #2DB7F5;
  background-color: #2DB7F5;
  color: #fff;
}
.m-button.m-button-type-plain.m-button-primary {
  color: #7952B3;
  border-color: #8E74B4;
}
.m-button.m-button-type-plain.m-button-primary:hover {
  border-color: #7952B3;
  background-color: #7952B3;
  color: #fff;
}
.m-button.m-button-type-plain.m-button-success {
  color: #03B976;
  border-color: #05a56a;
}
.m-button.m-button-type-plain.m-button-success:hover {
  border-color: #03B976;
  background-color: #03B976;
  color: #fff;
}
.m-button.m-button-type-plain.m-button-danger {
  color: #F56C6C;
  border-color: #F78989;
}
.m-button.m-button-type-plain.m-button-danger:hover {
  border-color: #F56C6C;
  background-color: #F56C6C;
  color: #fff;
}
.m-button.m-button-type-plain.m-button-warning {
  color: #F9AA40;
  border-color: #FAB458;
}
.m-button.m-button-type-plain.m-button-warning:hover {
  border-color: #F9AA40;
  background-color: #F9AA40;
  color: #fff;
}
.m-button-block + .m-button-block {
  margin-top: 5px;
}
.m-button-group {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.m-button-group .m-button {
  position: relative;
  float: left;
  border-radius: 0;
  margin-left: -1px;
}
.m-button-group .m-button:first-child {
  margin-left: 0;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.m-button-group .m-button:last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
/**
* animation
* e.g keyframes(effect-test, all, {css})
*/
@-webkit-keyframes btn-clicked-effect {
to {
    top: -6px;
    left: -6px;
    bottom: -6px;
    right: -6px;
    border-width: 6px;
    opacity: 0;
}
}
@keyframes btn-clicked-effect {
to {
    top: -6px;
    left: -6px;
    bottom: -6px;
    right: -6px;
    border-width: 6px;
    opacity: 0;
}
}
